import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import EntityActions from '../Redux/EntityRedux';
import IncomeForm from './IncomeForm';
import TransactionActions from '../Redux/TransactionRedux';
import BusinessCenterActions from '../Redux/BusinessCenterRedux';
import DataViewActions from '../Redux/DataViewRedux';

class IncomeContainer extends Component {
    state = {
        entity:{
            id: localStorage.getItem('entity_selected_id'),
            name: localStorage.getItem('entity_selected_name'),
        },
    }

    componentDidMount () {
        this.props.actions.view.transactionIncomeFormInit(this.state.entity.id);
    }

    checkViewData = () => {
        return this.props.view.data === null || !this.props.view.data.business_centers;
    }

    render() {
        const options = {
        };

        const viewData = this.props.view.data;

        if (this.checkViewData()){
            return  <div></div>
        }

        return (
            <IncomeForm
                entity={this.state.entity}
                business_centers={viewData.business_centers}
                options={options}
                onSave={this.props.actions.transaction.createIncomeTransaction}
                error_fields={this.props.transaction.error_fields}
            />
        )
    }
}

const mapStateToProps = (state) => ({
    entity : state.entity,
    business_center : state.business_center,
    transaction : state.transaction,
    view: state.data_view,
});

const mapDispatchToProps = (dispatch) => ({
	actions: {
        bc: bindActionCreators(BusinessCenterActions, dispatch),
        transaction: bindActionCreators(TransactionActions, dispatch),
        view: bindActionCreators(DataViewActions, dispatch),
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(IncomeContainer);


